<template>
  <div class="w-full h-160" ref="canvas">
    <Unity src="g/Build/game.json" width="1232" height="656" unityLoader="g/Build/g.asm.loader.js" ref="myInstance"></Unity>
  </div>
</template>
<script>
import * as PIXI from 'pixi.js';
import {gsap} from 'gsap';
import Unity from "@/components/UnityCanvas/Unity";

export default {
  name: 'PackOpening',
  components: {Unity},
  data() {
    return {}
  },
  methods: {},
  createVideoCanvas() {
    const app = new PIXI.Application({
      transparent: true,
      width: this.$refs.canvas.clientWidth,
      height: this.$refs.canvas.clientHeight
    })
    this.$refs.canvas.appendChild(app.view);

    const texture = PIXI.Texture.from('/pack.mp4');

    const videoSprite = new PIXI.Sprite(texture);
    videoSprite.width = app.screen.width;
    videoSprite.height = app.screen.height;
    const card = PIXI.Sprite.from('/1.png');
    card.anchor.set(0.5);
    card.x = app.screen.width / 2;
    card.y = app.screen.height / 2 + 75;
    card.alpha = 0;
    card.scale.set(2.5);
    app.stage.addChild(videoSprite);
    app.stage.addChild(card);
    const videoController = videoSprite.texture.baseTexture.resource.source;

    let isShow = false;

    function step() {
      if (videoController.currentTime > 6.5 && !isShow) {
        isShow = true;
        card.alpha = 1;
        // gsap.to(card, {
        //   alpha: 0.5, duration: 4, repeat: 0,
        // });
        gsap.to(card.scale, {
          x: 6, y: 6, duration: 4, repeat: 0, onComplete: () => {
            card.alpha = 0;
          }
        });
      }
      window.requestAnimationFrame(step);
    }

    window.requestAnimationFrame(step);
  }
}
</script>
