<template>
  <div class="full-card" :style="{fontSize: (this.baseFontSize) + 'px' }" ref="root">
    <canvas :width="canvasWidth * canvasScale" :height="canvasHeight * canvasScale" ref="canvas"></canvas>
    <div class="player-overview">
      <div class="rating">99</div>
      <div class="position">ST</div>
    </div>
    <div class="name" :style="{fontSize: (this.baseFontSize * 0.8) + 'px' }">Martnez</div>
    <div class="stats" :style="{fontSize: (this.baseFontSize * 0.6) + 'px' }">
      <div class="stats-row">
        <div class="stat-cell">
          <span class="Pace stat-value" style="color: rgb(68, 58, 34);">67</span>
          <span class="Pace stat-label" style="color: rgb(87, 74, 43);">PAC</span>
        </div>
        <div class="stat-cell">
          <span class="Dribbling stat-value" style="color: rgb(68, 58, 34);">71</span>
          <span class="Dribbling stat-label" style="color: rgb(87, 74, 43);">DRI</span>
        </div>
      </div>
      <div class="stats-row">
        <div class="stat-cell">
          <span class="Shooting stat-value" style="color: rgb(68, 58, 34);">74</span>
          <span class="Shooting stat-label" style="color: rgb(87, 74, 43);">SHO</span>
        </div>
        <div class="stat-cell">
          <span class="Defending stat-value" style="color: rgb(68, 58, 34);">39</span>
          <span class="Defending stat-label" style="color: rgb(87, 74, 43);">DEF</span>
        </div>
      </div>
      <div class="stats-row">
        <div class="stat-cell">
          <span class="Passing stat-value" style="color: rgb(68, 58, 34);">62</span>
          <span class="Passing stat-label" style="color: rgb(87, 74, 43);">PAS</span>
        </div>
        <div class="stat-cell">
          <span class="Header stat-value" style="color: rgb(68, 58, 34);">76</span>
          <span class="Header stat-label" style="color: rgb(87, 74, 43);">PHY</span>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
.full-card {
  position: relative;
  cursor: pointer;

  canvas {
    width: 100%;
    height: 100%;
  }

  .player-overview {
    text-align: center;
    position: absolute;
    left: 17%;
    top: 17%;
    line-height: 115%;
    width: 25%;
    font-size: 70%;
    font-weight: bold;

    .rating {
      font-size: 145%;
    }
  }

  .name {
    position: absolute;
    width: 100%;
    top: 52%;
    overflow: hidden;
    padding: 0 12% 0 12%;
    box-sizing: border-box;
    line-height: 150%;
    text-align: center;
    text-transform: uppercase;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: rgb(68, 58, 34);
    font-weight: bold;
  }

  .stats {
    position: absolute;
    top: 64%;
    left: 0;
    width: 100%;
    height: 18.57%;
    text-align: center;
    line-height: 110%;
    font-size: 90%;

    .stats-row {
      display: flex;
      margin: 0 auto;
      text-align: left;
      width: 68%;

      .stat-cell {
        display: flex;
        width: 50%;
        height: 33.33%;
        box-sizing: border-box;

        &:nth-child(odd) {
          padding-left: 12%;
          padding-right: 10%;
        }

        &:nth-child(even) {
          padding-left: 10%;
          padding-right: 12%;
        }

        .stat-value {
          flex-basis: 40%;
          font-weight: 600;
        }

        .stat-label {
          flex-basis: 60%;
          font-weight: 300;
          padding-left: 15%;
          position: relative;
          text-align: left;
        }
      }
    }
  }
}
</style>


<script>

import {CARD_DRAW_DETAILS} from "@/constants/card.constants";

export default {
  name: 'FullCard',
  data() {
    return {
      canvasWidth: CARD_DRAW_DETAILS.LARGE.SHELL.width,
      canvasHeight: CARD_DRAW_DETAILS.LARGE.SHELL.height,
      canvasScale: 3,
      baseFontSize: 16,
      fontScale: 8
    }
  },
  components: {},
  methods: {
    // _drawTransparentBar() {
    //   if (this.renderSpecial) {
    //     var e, t;
    //     var i = this.getHeight()
    //         , s = this._ctx.createLinearGradient(0, 0, 24, i)
    //         , n = this._colormap.background
    //         , o = "rgba(" + n.r + ", " + n.g + ", " + n.b + ", 0.675)"
    //         , a = "rgba(" + n.r + ", " + n.g + ", " + n.b + ", 0)";
    //     this._maskItem(),
    //         s.addColorStop(0, a),
    //         s.addColorStop(.1, a),
    //         s.addColorStop(.333, o),
    //         s.addColorStop(.666, o),
    //         s.addColorStop(.9, a),
    //         s.addColorStop(1, a),
    //         t = this.size === enums.ItemViewType.LARGE ? (e = 30,
    //             27) : (e = 13,
    //             20),
    //         this._ctx.beginPath(),
    //         this._ctx.fillStyle = s,
    //         this._ctx.fillRect(e, 0, t, i),
    //         this._resetMask()
    //   }
    // },
    // _drawAsset(e) {
    //   if (this._ctx) {
    //     const t = this.assets[e];
    //     if (!t || !t.element || !t.element.complete)
    //       return;
    //     const i = t.element.width || t.width
    //         , s = t.element.height || t.height;
    //     this._ctx.drawImage(t.element, 0, 0, i, s, t.xPos, t.yPos, t.width, t.height)
    //   }
    // },
    _drawDivider(e, t, i, s) {
      const ctx = this.$refs.canvas.getContext('2d');
      ctx.beginPath();
      ctx.moveTo(e, t);
      ctx.lineTo(i, s);
      ctx.strokeStyle = '#000000';
      ctx.stroke();
    },
    _drawCard() {
      const ctx = this.$refs.canvas.getContext('2d');
      const bgImg = new Image();
      bgImg.onload = () => {
        ctx.drawImage(bgImg, CARD_DRAW_DETAILS.LARGE.SHELL.xPos,
            CARD_DRAW_DETAILS.LARGE.SHELL.yPos,
            CARD_DRAW_DETAILS.LARGE.SHELL.width * this.canvasScale,
            CARD_DRAW_DETAILS.LARGE.SHELL.height * this.canvasScale);
      };
      bgImg.src = require('@/assets/images/fifa21/goldrare (1).webp');

      const playerImg = new Image();
      playerImg.onload = () => {
        ctx.drawImage(playerImg, 0, 0, playerImg.width, playerImg.height,
            CARD_DRAW_DETAILS.LARGE.MAIN.SPECIAL.xPos * this.canvasScale,
            CARD_DRAW_DETAILS.LARGE.MAIN.SPECIAL.yPos * this.canvasScale,
            CARD_DRAW_DETAILS.LARGE.MAIN.SPECIAL.width * this.canvasScale,
            CARD_DRAW_DETAILS.LARGE.MAIN.SPECIAL.height * this.canvasScale);
      };
      playerImg.src = require('@/assets/images/players/p100909443.png');

      const badgeImg = new Image();
      badgeImg.onload = () => {
        ctx.drawImage(badgeImg, 0, 0, badgeImg.width, badgeImg.height,
            CARD_DRAW_DETAILS.LARGE.BADGE.xPos * this.canvasScale,
            CARD_DRAW_DETAILS.LARGE.BADGE.yPos * this.canvasScale,
            CARD_DRAW_DETAILS.LARGE.BADGE.width * this.canvasScale,
            CARD_DRAW_DETAILS.LARGE.BADGE.height * this.canvasScale);
      };
      badgeImg.src = require('@/assets/images/clubs/11.png');

      const flagImg = new Image();
      flagImg.onload = () => {
        ctx.drawImage(flagImg, 0, 0, flagImg.width, flagImg.height,
            CARD_DRAW_DETAILS.LARGE.FLAG.xPos * this.canvasScale,
            CARD_DRAW_DETAILS.LARGE.FLAG.yPos * this.canvasScale,
            CARD_DRAW_DETAILS.LARGE.FLAG.width * this.canvasScale,
            CARD_DRAW_DETAILS.LARGE.FLAG.height * this.canvasScale);
      };
      flagImg.src = require('@/assets/images/nations/27.png');

      this._drawDivider(34, 131.5, 117, 131.5);
      this._drawDivider((this.canvasWidth - 1) / 2, 135, (this.canvasWidth - 1) / 2, 171);
      this._drawDivider(68, 174, 84, 174);
      this._drawDivider(36, 66.5, 51, 66.5);
      this._drawDivider(36, 85.5, 51, 85.5);
    }
  },

  mounted() {
    this._drawCard();
    this.baseFontSize = this.$refs.root.clientWidth / this.fontScale;
  }
}
</script>
