<template>
  <div class="tiny-card" :style="{fontSize: (this.baseFontSize) + 'px' }" ref="root">
    <canvas :width="canvasWidth * canvasScale" :height="canvasHeight * canvasScale" ref="canvas"></canvas>
    <div class="player-overview">
      <div class="rating">99</div>
      <div class="position">ST</div>
    </div>
  </div>
</template>

<style lang="scss">
.tiny-card {
  position: relative;
  cursor: pointer;

  canvas {
    width: 100%;
    height: 100%;
  }

  .player-overview {
    text-align: center;
    position: absolute;
    left: 11%;
    top: 23%;
    line-height: 115%;
    width: 25%;
    font-size: 70%;
    font-weight: bold;

    .rating {
      font-size: 145%;
    }

  }

  &:hover {
    &:before {
      -webkit-transform: translateX(300px) skewX(-15deg);
      transform: translateX(300px) skewX(-15deg);
      opacity: 0.6;
      -webkit-transition: .7s;
      transition-duration: .7s;
    }
  }

  &:before {
    content: '';
    display: block;
    position: absolute;
    background: rgba(255, 255, 255, 0.5);
    width: 60px;
    height: 70%;
    left: 0;
    top: 15%;
    opacity: 0;
    -webkit-filter: blur(30px);
    filter: blur(30px);
    -webkit-transform: translateX(-100px) skewX(-15deg);
    transform: translateX(-100px) skewX(-15deg);
  }

}
</style>


