    return {
      canvasWidth: CARD_DRAW_DETAILS.TINY.SHELL.width,
      canvasHeight: CARD_DRAW_DETAILS.TINY.SHELL.height,
      canvasScale: 3,
      baseFontSize: 16
    }
  },
  components: {},
