    _drawCard() {
      const ctx = this.$refs.canvas.getContext('2d');
      const bgImg = new Image();
      bgImg.onload = () => {
        ctx.drawImage(bgImg, CARD_DRAW_DETAILS.TINY.SHELL.xPos,
            CARD_DRAW_DETAILS.TINY.SHELL.yPos,
            CARD_DRAW_DETAILS.TINY.SHELL.width * this.canvasScale,
            CARD_DRAW_DETAILS.TINY.SHELL.height * this.canvasScale);
      };
      bgImg.src = require('@/assets/images/fifa21/goldrare (1).webp');

      const playerImg = new Image();
      playerImg.onload = () => {
        ctx.drawImage(playerImg, 0, 0, playerImg.width, playerImg.height,
            CARD_DRAW_DETAILS.TINY.MAIN.SPECIAL.xPos * this.canvasScale,
            CARD_DRAW_DETAILS.TINY.MAIN.SPECIAL.yPos * this.canvasScale,
            CARD_DRAW_DETAILS.TINY.MAIN.SPECIAL.width * this.canvasScale,
            CARD_DRAW_DETAILS.TINY.MAIN.SPECIAL.height * this.canvasScale);
      };
      playerImg.src = require('@/assets/images/players/p100909443.png');

      const badgeImg = new Image();
      badgeImg.onload = () => {
        ctx.drawImage(badgeImg, 0, 0, badgeImg.width, badgeImg.height,
            CARD_DRAW_DETAILS.TINY.BADGE.xPos * this.canvasScale,
            CARD_DRAW_DETAILS.TINY.BADGE.yPos * this.canvasScale,
            CARD_DRAW_DETAILS.TINY.BADGE.width * this.canvasScale,
            CARD_DRAW_DETAILS.TINY.BADGE.height * this.canvasScale);
      };
      badgeImg.src = require('@/assets/images/clubs/11.png');

