import homePage from '../pages/Home.vue'
import collectionPage from '../pages/Collections.vue'
import loginPage from '../pages/Login.vue'
import playPage from '../pages/Play.vue'
import clubPage from '../pages/Club.vue'
import storePage from '../pages/Store.vue'
import singleDraftPage from '../pages/SingleDraft.vue'
import notFoundPage from '../pages/NotFound.vue'
import packOpeningPage from '../pages/PackOpening.vue'
import {routePropResolver} from './utils'
import {DOMAIN_TITLE} from '@/.env'
import {ROUTE_TYPE} from "@/constants/router.constants";

export const routes = [
  {
    path: '/',
    name: 'index',
    component: homePage,
    meta: {title: `${DOMAIN_TITLE} | home`, type: ROUTE_TYPE.MENU, order: 1}
  },
  {
    path: '/collections',
    name: 'collections',
    component: collectionPage,
    meta: {title: `${DOMAIN_TITLE} | news`, type: ROUTE_TYPE.MENU, order: 4},
    props: routePropResolver
  },
  {
    path: '/play',
    name: 'play',
    component: playPage,
    meta: {title: `${DOMAIN_TITLE} | profile`, type: ROUTE_TYPE.MENU, order: 2}
  },
  {
    path: '/club',
    name: 'club',
    component: clubPage,
    meta: {title: `${DOMAIN_TITLE} | club`, type: ROUTE_TYPE.MENU, order: 2}
  },
  {
    path: '/store',
    name: 'store',
    component: storePage,
    meta: {title: `${DOMAIN_TITLE} | store`, type: ROUTE_TYPE.MENU, order: 2}
  },
  {
    path: '/transfer',
    name: 'transfer',
    component: playPage,
    meta: {title: `${DOMAIN_TITLE} | profile`, type: ROUTE_TYPE.MENU, order: 5}
  },
  {
    path: '/objectives',
    name: 'objectives',
    component: playPage,
    meta: {title: `${DOMAIN_TITLE} | profile`, type: ROUTE_TYPE.MENU, order: 3}
  },
  {
    path: '/login',
    name: 'login',
    component: loginPage,
    meta: {title: `${DOMAIN_TITLE} | login`, type: ROUTE_TYPE.MENU, order: 7}
  },
  {
    path: '/single-draft',
    name: 'single-draft',
    component: singleDraftPage,
    meta: {title: `${DOMAIN_TITLE} | SingleDraft`, type: ROUTE_TYPE.MENU, order: 7}
  },
  {
    path: '/pack-open',
    name: 'pack-open',
    component: packOpeningPage,
    meta: {title: `${DOMAIN_TITLE} | PackOpen`, type: ROUTE_TYPE.MENU, order: 7}
  },
  {
    path: '*',
    component: notFoundPage,
    meta: {title: `${DOMAIN_TITLE} | not found`}
  }
]
