<template>
  <div class="w-full h-full bg-fixed bg-cover" :style="{ backgroundImage: `url('${bgImage}')` }">
    <div class="container mx-auto min-h-screen flex flex-col bg-scroll md:p-2 xl:p-6 lg:p-4 sm:p-1">
      <AppHeader/>
      <div class="flex-1 flex items-center">
        <transition :name="transitionName" mode="out-in">
          <router-view/>
        </transition>
      </div>
      <PackOpenModal v-show="this.$store.state.pack.openPackAnimation"/>
    </div>
  </div>
</template>

<script>
import AppHeader from './Header.vue'
import {ROUTE_TYPE} from "@/constants/router.constants";
import PackOpenModal from "@/components/PackOpenModal";

const DEFAULT_TRANSITION = 'slide-right';
export default {
  name: 'AppLayout',
  data() {
    return {
      transitionName: DEFAULT_TRANSITION,
      bgImage: require('@/assets/images/bg2.jpg')
    }
  },
  created() {
    this.$router.beforeEach((to, from, next) => {
      const isMenu = to.meta.type === ROUTE_TYPE.MENU || from.meta.type === ROUTE_TYPE.MENU;
      let transitionName = DEFAULT_TRANSITION;
      if (isMenu) {
        transitionName = to.meta.order < from.meta.order ? 'slide-right' : 'slide-left';
      }
      this.transitionName = transitionName;
      next();
    });
  },
  components: {
    AppHeader,
    PackOpenModal
  }
}
</script>s
